<template>
  <div>
    <!-- <div class="rightList" @click="showSelect">
      <input placeholder="" v-model="cityEx" />
    </div>-->
    <div class="input-block">
      <div class="input-name title1"></div>
      <input class="input-input" placeholder="" @click="showSelect" v-model="cityEx" />
    </div>
  </div>
</template>

<script>
import '../util/iosSelect/iosSelect.css';
import '../util/iosSelect/iosSelect.js';

//three
// import './zepto.js';
//areaData_v2data format error
import {
    iosProvinces,
    iosCitys,
    iosCountys,
} from '../util/iosSelect/areaData_v2.js';

export default {
    name: 'AddressSelector',
    data() {
        return {
            cityEx: '  ',
        };
    },
    methods: {
        //
        showSelect: function() {
            //id
            var levels = this.textToLevel(this.cityEx);

            var iosSelect = new IosSelect(
                3,
                [iosProvinces, iosCitys, iosCountys],
                {
                    title: '',
                    itemHeight: 35,
                    relation: [1, 1], //
                    oneLevelId: levels[0],
                    twoLevelId: levels[1],
                    threeLevelId: levels[2], //
                    callback: (selectOneObj, selectTwoObj, selectThreeObj) => {
                        this.cityEx =
                            selectOneObj.value +
                            ' ' +
                            selectTwoObj.value +
                            ' ' +
                            selectThreeObj.value;
                        console.log(this.cityEx);
                    },
                }
            );
        }, //leveliosSelect
        textToLevel: function(text) {
            var arr = [];

            if (text) {
                var objs = text.split(' ');

                if (objs[0]) {
                    iosProvinces.forEach((item, index) => {
                        if (item.value == objs[0]) {
                            arr.push(item.id);
                        }
                    });
                }

                if (objs[1]) {
                    iosCitys.forEach((item, index) => {
                        if (item.value == objs[1]) {
                            arr.push(item.id);
                        }
                    });
                }

                if (objs[2]) {
                    iosCountys.forEach((item, index) => {
                        if (item.value == objs[2]) {
                            arr.push(item.id);
                        }
                    });
                }
            }

            return arr;
        },
    },
};
</script>
